const string = `
.container *,
.container *::before,
.container *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

/* å¸ƒç½®ä¸€ä¸‹èƒŒæ™¯,ç»™é›·è¾¾ä¸€ä¸ªå±…ä¸­*/
.container {
    width: 100%;
    min-height: 50vh;
    padding: 0px 0;
    text-align: center;
    overflow: hidden;
    background-image: linear-gradient(90deg, rgb(89, 76, 75), rgb(8, 9, 2));
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: row wrap;
}

/* è®¾è®¡ä¸€ä¸‹è¡¨ç›˜*/
.container .randar-box {
    width: 360px;
    height: 360px;
    position: relative;
    display: flex;
    align-items: center;
    margin-top: 20px;

    --green: hsl(134, 34%, 38%);
    --line-color: #212121;
}

.container .randar-box .randar-content {
    /* å…ˆå®šä½ä¸€ä¸‹è¡¨ç›˜*/
    display: flex;
    width: 320px;
    height: 320px;
    border-radius: 50%;
    border: 15px solid rgb(243, 236, 236);
    /* çº¿æ€§é‡å¤æ¸å˜ç»˜åˆ¶åœ°å›¾*/
    background: radial-gradient(#afadad 50%, #0a0a0a),
        repeating-linear-gradient(0deg,
            var(--line-color) 0px,
            var(--line-color) 1px,
            transparent 1px,
            transparent 25px),
        repeating-linear-gradient(90deg,
            var(--line-color) 0px,
            var(--line-color) 1px,
            transparent 1px,
            transparent 25px),
        linear-gradient(135deg, var(--green), var(--green));
    /* è®¾ç½®ä¸€ä¸‹èƒŒæ™¯æ··åˆæ¨¡å¼ï¼Œå¹¶ä¸”è°ƒæ•´ç½‘æ ¼çš„ä½ç½®ã€‚è®©ç½‘æ ¼å’ŒèƒŒæ™¯èžåˆ*/
    background-blend-mode: soft-light;
    background-position-x: 20px;
    background-position-y: 7px;
    will-change: transform;
}

/* ç”¨ä¼ªå…ƒç´ ä¿®æ­£ä¸€ä¸‹é›·è¾¾çš„è¾¹æ¡†*/
.container .randar-box .randar-content::before {
    content: "";
    display: block;
    width: 296px;
    height: 296px;
    position: absolute;
    border-radius: 50%;
    border: 1px solid #355555;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    box-shadow: inset 0 0 20px 2px #333,
        0 0 12px #999;

}

/* å®šä½ä¸€ä¸‹è‡ªå·±*/
.container .randar-box .randar-content .randar-pos {
    width: 0;
    height: 0;
    position: absolute;
    border-width: 12px 10px;
    border-style: solid;
    border-color: transparent transparent #ca4828 transparent;
    left: 50%;
    top: 50%;
    margin-left: -10px;
    margin-top: -18px;
}

/* è®¾è®¡ä¸€ä¸‹æ•Œäºº*/

.randar-ball {
    width: 10px;
    height: 10px;
    background: #f7f459;
    position: absolute;
    border-radius: 50%;
    box-shadow: 0px 0px 5px #ecea68;
    animation: flash 3.1s linear infinite;
}

.randar-ball:nth-of-type(1) {
    left: 29px;
    top: 94px;
}

.randar-ball:nth-of-type(2) {
    left: 58px;
    top: 65px;
}

.randar-ball:nth-of-type(3) {
    left: 56px;
    top: 78px;
}

.randar-ball:nth-of-type(4) {
    left: 204px;
    top: 228px;
}

/* åŠ ä¸ªé—ªçƒ*/
@keyframes flash {

    0%,
    50%,
    to {
        opacity: 1;
    }

    25%,
    75% {
        opacity: 0.1;
    }
}
/*åŠ ä¸€ä¸ªç‚«é…·çš„æ‰«æåŠ¨ç”»*/
.randar-content::after {
    content: "";
    background: linear-gradient(var(--green) 0%, black 60%), linear-gradient(-90deg, black 50%, var(--green) 50%);
    background-blend-mode: darken;
    position: absolute;
    width: 290px;
    height: 290px;
    margin-left: -145px;
    margin-top: -145px;
    left: 50%;
    top: 50%;
    border-radius: 50%;
    will-change: opacity;
    opacity: 0.15;
    z-index: 88;
    background-blend-mode: darken;
    animation: scan 5s linear infinite;
}

@keyframes scan {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}
`
export default string